<template>
    <div class="iimg">
        <div class="img-wrapper" @click="viewImg(img)">
            <img :src="img.imgUrl">
        </div>
        <div class="desc">
            <div class="title">{{img.title}}</div>
            <div class="profile">{{img.profile}}</div>
        </div>
        <div class="price" v-if="img.price">
            挂牌价：￥{{img.price}} 元/套
        </div>
        <div class="btn"  @click="viewImg(img)">
            <span class="iconfont icon-qianjin"></span>
        </div>
    </div>
</template>
<script>
export default {
    props:{
        img: {
            type: Object,
            default: () => new Object()
        }
    },
    methods: {
        viewImg (img) {
            this.$emit('selectimg', img)
        }
    },
}
</script>
<style lang="less" scoped>
.iimg{
    height: 100%;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    .img-wrapper{
        height: 180px;
        flex: 0 0 190px;
        margin: 0 20px 0 10px;
        border-radius: 3px;
        overflow: hidden;
        box-shadow: 0 0 2 #ccc;
        cursor: pointer;
        img{
            height: 100%;
            width: 100%;
            transition: all .8s;
            &:hover{
                transform: scale(1.2);
            }
        }
    }
    .desc{
        height: 180px;
        flex-grow: 1;
        text-align: left;
        .title{
            max-width: 280px;
            color: #e0c073;
            font-size: 20px;
            margin-top: 8px;
            transition: all .5s;
            position: relative;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            &::before{
                content: '';
                position: absolute;
                top: 50%; left: 0;
                width: 0;
                height: 2px;
                background-color: #e0c073;
                transition: all .5s;
            }
        }
        .profile{
            padding: 10px 10px 0 0;
            line-height: 25px;
        }
        &:hover{
            .title{
                padding-left: 40px;
                &::before{
                    width: 30px;
                }
            }
        }
    }
    .btn{
        height: 26px;width: 26px;
        position: absolute;
        bottom: 15px;
        right: 15px;
        border-radius: 50%;
        border: 1px solid #e0c073;
        color: #e0c073;
        line-height: 26px;
        transition: all .5s;
        cursor: pointer;
        &:hover{
            transform: rotate(360deg);
            color: white;
            background-color: #e0c073;
        }
    }
    .price{
        position: absolute;
        bottom: 20px; right: 60px;
    }
}
@media screen and(max-width: 1100px) {
    .iimg {
        .img-wrapper {
            height: 160px;
            flex: 0 0 170px;
        }
    }
    .desc {
        .profile {
            font-size: 14px;
        }
    }
    .price{
        font-size: 14px;
        right: 50px!important;
    }
}
@media screen and(max-width: 400px) {
    .iimg {
        .img-wrapper {
            height: 120px;
            flex: 0 0 130px;
        }
    }
    .desc {
        .profile {
            font-size: 14px;
        }
    }
    .price{
        
    }
}
</style>




