<template>
    <div class="imglist">
        <div class="img-item" v-for="(item, index) in list" :key="index">
            <Iimg :img='item' @selectimg='readImg(item)'/>
        </div>
        <div class="bottom">
            <p @click="getPage(-1)">上一页</p>
            <p>第 {{page}} 页</p>
            <p @click="getPage(1)">下一页</p>
        </div>
    </div>
</template>
<script>
import Iimg from '_c/iimg2'
import { getImageList } from '@/api'
export default {
    data() {
        return {
            list: [],
            page: 1,
            size: 10,
            cateid: 0,
            cateindex: 0,
            ismore: true
        }
    },
    mounted() {
        let { cateid, cateindex } = this.$route.query
        this.cateid = cateid
        this.cateindex = cateindex
        this.getImgList(cateid, cateindex)
    },
    methods: {
        async getImgList (cateid, cateindex, page) {
            let res = await getImageList(cateid, cateindex, page, this.size)
            this.list = res
            if (this.list.length < 10) {
                this.ismore = false
            } else {
                this.ismore = true
            }
        },
        readImg (item) {
            this.$router.push({
                path: '/imgreader', 
                query: { 
                    id: item.id
                }})
        },
        getPage (n) {
            if (n === 1 && this.ismore) {
                this.page++
            }
            if (n === -1) {
                this.page = this.page - 1 > 1 ? this.page - 1 : 1
            }
        }
    },
    watch: {
        page (newpage) {
            this.getImgList(this.cateid, this.cateindex, newpage)
        }
    },
    components: {
        Iimg
    }
}
</script>
<style lang="less" scoped>
.imglist{
    padding: 40px 0;
    .img-item{
        width: 48%;
        box-shadow: 0 0 3px #ccc;
        height: 200px;
        margin: 20px 10px;
        &:nth-of-type(2n - 1){
            float: left;
        }
        &:nth-of-type(2n){
            float: right;
        }
    }
    .bottom{
        width: 100%;
        display: flex;
        justify-content: space-around;
        p{
            padding: 10px 0;
            line-height: 25px;
            color: #e0c073;
            cursor: pointer;
        }
    }
    &::after{
        content: '';
        display: block;
        clear: both;
    }
}
@media screen and(max-width: 1100px) {
    .imglist{
        .img-item{
            margin: 0 1%;
            height: 180px;
        }
    }
}
@media screen and(max-width: 800px) {
    .imglist{
        .img-item{
            width: 90%;
            margin: 10px auto;
            box-sizing: border-box;
            float: none!important;
        }
    }
}
@media screen and(max-width: 400px) {
    .imglist{
        .img-item{
            height: 160px;
            width: 100%;
            margin: 8px auto;

        }
    }
}
</style>



